label {
	display: inline-flex;
	align-items: center;
	user-select: none;
}

input[type="radio"],
input[type="checkbox"] {
	appearance: none;
	-webkit-appearance: none;
	-moz-appearance: none;
	margin: 0;
	background: 0;
	position: fixed;
	opacity: 0;
	border: none;
}

